@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Raleway:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}button{height:30px}ul{list-style-type:none;padding:0}header{top:0}h1{font-family:'Raleway', sans-serif}.wrapper{display:flex;width:100;align-items:stretch}#sidebar.active{position:absolute;left:-9999px}.App-logo{height:40vmin;pointer-events:none}.container{width:30rem;margin-left:auto;margin-right:auto}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:row-reverse;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white;scroll-behavior:initial}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}:root{font-family:'Open Sans', sans-serif;font-size:27px}.info{display:flex;justify-content:center}.background{height:-webkit-fill-available;display:flex;margin-bottom:2rem}.front-page{display:flex;justify-content:center;flex-direction:column}.fade-in-section{opacity:0;transform:translateY(20vh);visibility:hidden;transition:opacity 0.6s ease-out, transform 1.2s ease-out;will-change:opacity, visibility}.fade-in-section.is-visible{opacity:1;transform:none;visibility:visible}body::-webkit-scrollbar{width:0.25rem}body::-webkit-scrollbar-track{background:#1e1e24}body::-webkit-scrollbar-thumb{background:#61dafb}


.container {
    position: relative;
}

img{
    width: 10rem;
}

img{
    width: 30rem;
}
.nav-bar{
    position: fixed;
    display: none;
    width: 5rem;
    height: 40vh;
    background-color: teal;
    transition: 200ms ease;
    margin-top: 10rem;
    margin-left: 2rem;
  }

.nav-bar-nav {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-self: center;
    height: 40vh;
    justify-content: space-around;

}

.nav-item{
    width: 100%;
    transition: 600ms;
    font-size: 15px;
    height: 10vh;
}

ul{
    display: flex;
    justify-content: space-around;
    padding: 0rem;
    list-style: none;
}

a{
    padding-left: 0rem;
}

.chu {
    margin-top: 0.5rem;
    margin-left: 0.5rem;
}

.nav-item:last-child{
    /* margin-top: auto; */
}

.nav-link{
    display: flex;
    align-items: center;
    -webkit-filter: grayscale(100%) opacity(0.7);
            filter: grayscale(100%) opacity(0.7);
    transition: 600ms;
}

.nav-link:hover {
    -webkit-filter: grayscale(0%) opacity(1);
            filter: grayscale(0%) opacity(1);
}

.highlight {
    background-color: lavender;
}

.link-text{
    display: none;
    margin-left: 1rem;
    margin-top: 1rem;
}

.nav-link svg {
    min-width: 3rem;
    margin-left: 1rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
}

/* .nav-bar:hover{
    width: 16rem;
} */

/* .nav-bar:hover .link-text{
    display: block;
    color: red;
} */

.fa-primary {
    color: #ff7eee;
  }
  
  .fa-secondary {
    color: #df49a6;
  }
  
  .fa-primary,
  .fa-secondary {
    transition: 600ms;
  }

  a{
      text-decoration: none;
      color: black;
  }

  a:hover{
      text-decoration: underline;
  }
.text {
    width: 30rem;
    justify-content: space-around;
    float: left;
}

.inner{
    font-size: 22px;
    font-weight: 450;
    margin-bottom: 1rem;
    word-spacing: 0.1rem;
}
